
.ClientSchedule{
    height: 54.3rem;
    background: -webkit-gradient(linear, left top, right bottom, from(#6d2e46), to(#9A94BC));
    background: -webkit-linear-gradient(top left, #6d2e46, #9A94BC);
    background: -o-linear-gradient(top left, #6d2e46, #9A94BC);
    background: linear-gradient(to bottom right, #6d2e46, #9A94BC);
}

.ClientSchedule .Container{
    margin: 2%;
    
}



.ClientSchedule .Title {
    margin-right: 15%;
    font-size: 25pt;
    color: white;
    text-shadow: 2px #000000;
    font-family: 'Poiret One', cursive;
    font-stretch: expanded;
    -ms-flex: flex 1;
        flex: flex 1;
    text-align: center;
    margin-bottom: 2%;
}

.Services {
    padding: 2%;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
    border: 5px solid #6d2e46;

}

.button {
    background-color: #6d2e46;
}


.Services .Button{
    
    -webkit-box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    
            box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 140px;
    height: 45px;
    font-family: 'Roboto', sans-serif; 
    font-size: 11px;
    text-transform: uppercase;
    font-weight: bold;
    color: #000000;
    background-color: #ffff;
    border: none;
    border-radius: 45px;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
    margin-bottom: 10px;
    
    
}

.Services .Button:hover {
    background-color: #6d2e46;
    -webkit-box-shadow: 0px 7px 12px #6d2e46;
            box-shadow: 0px 7px 12px #6d2e46;
    color: #ffffff;
    -webkit-transform: translate(-5px);
        -ms-transform: translate(-5px);
            transform: translate(-5px);
}







